import * as Styled from './StyledHeader'

export default function Header ({children}) {
  return <> {children} </>
}

Header.Frame = ({children}) => {
  return (
    <Styled.Frame>{children}</Styled.Frame>
  )
}


Header.Title = ({children, mobile}) => {
  return (
    <Styled.PageTitle mobile={mobile}>{children}</Styled.PageTitle>
  )
}

Header.Nav = ({children}) => {
  return (
    <Styled.Nav>
      <ul>
        {children}
      </ul>
    </Styled.Nav>
  )
}

Header.Link = ({to, activeClassName, children }) => {
  return (
    <li>
      <Styled.SNavLink 
        to={to}
        activeClassName={activeClassName}
        exact
      >
        {children}
      </Styled.SNavLink>
    </li>

  )
}

Header.Cart = ({ to, children }) => (
  <li>
    <Styled.CartButton as="NavLink" to={to}>{children}</Styled.CartButton>
  </li>
)


 