import Header from "../Components/Header"
import  DropDown  from "../Components/ReUsable/Dropdown/index"

export default function HeaderContainer () {
  return (
    <Header>
      <Header.Title mobile={true}>React Page</Header.Title>
      <Header.Frame>
        <Header.Title mobile={false}>React Page</Header.Title>
        <Header.Nav>
        
          <Header.Link
            to='/'
          >
            Home
          </Header.Link>
          <Header.Link
            to='/category/electronics'
          >
            Electronics
          </Header.Link>
          <Header.Link
            to='/category/jewelry'
          >
            Jewelry
          </Header.Link>

          <DropDown title="Clothing" >
            <Header.Link
              to='/category/electronics'
            >
              Electronics
            </Header.Link>
            <Header.Link
              to='/category/jewelry'
            >
              Jewelry
            </Header.Link>
          </DropDown>

          <Header.Cart
            to="/cart"
          >
            Cart
          </Header.Cart>
        </Header.Nav>
      </Header.Frame>
    </Header>
  )
}