import { StyledDropDown, StyledChevronDown, Dropper } from "./styles";
import { useEffect, useState, useRef } from "react";


export default function DropDown ({ title, children }) {
  const [open, setOpen] = useState(false)
  const dropper = useRef(null)

  const collapse = () => {
    let sectionHeight = dropper.current.scrollHeight
    let elementTransition = dropper.current.style.transition
    dropper.current.style.transition = ''
    requestAnimationFrame(() => {
      dropper.current.style.height = sectionHeight + 'px'
      dropper.current.style.transition = elementTransition
      requestAnimationFrame(() => {
        dropper.current.style.height = 0 + 'px'
      })
    })
  }
  const expand = () => {
    let sectionHeight = dropper.current.scrollHeight
    dropper.current.style.height = sectionHeight + 'px'  
  }

  useEffect(() => {
    if (open) {
      expand()
    } else {
      collapse()
    }
    return expand()
  }, [open])
  
  const toggle = () => {
    setOpen(openC => !openC)
  }


  return ( 
    <StyledDropDown>
      <div as="li"  onClick={toggle}  className="button">
        {title} 
        <StyledChevronDown open={open} size={24} />
      </div>
      <Dropper ref={dropper} open={open}>
        {children}
      </Dropper>
    </StyledDropDown>
  )

} 